@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
h2 {
    text-align: center;
    font-family: 'Indie Flower', cursive;
    margin-top: 2rem;
    margin-bottom: 3rem;
    font-size: 2rem;
}

@media screen and (max-width: 480px) {
    h2 {
        text-align: center;
        font-family: 'Indie Flower', cursive;
        margin-bottom: 2rem;
        font-size: 2rem;
    } 
}

.logoImage {
    display: flex;
    flex-direction: column;
    background-position: center;
    background-image: url("https://www.clipartmax.com/png/full/20-209727_ballpoint-pen-clip-art-fountain-pen-clipart.png");
    background-size: contain;
    background-repeat: no-repeat;
    align-items: center;
    justify-content: center;
    height: 10rem;
    margin-top: 2rem;
}

.entry-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;
}

@media screen and (max-width: 480px) { 
    .entry-form {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 2rem;
    }

}

button {
    background-color: transparent;
    border: none;
    font-size: 1.3rem;
    font-weight: bold;
    font-family: 'Indie Flower', cursive;
    height: 3rem;
    width: 10rem;  
    margin-left: 1rem;
    margin-right: 1rem;
}

button:hover {
    color: blue;
  }

.SentenceGenerator {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h3 {
    margin-top: 3rem;
    margin-bottom: 3rem;
    margin-left: 5rem;
    margin-right: 5rem;
    font-size: 1.5rem;
    font-family: 'Indie Flower', cursive;
}

@media only screen and (max-width: 500px) {
  h3 {
    margin-left: 1rem;
    margin-right: 1rem;
  }

}

@media only screen and (max-width: 500px) {
    .radioform {
      display: flex;
      flex-direction: column; 
      align-items: center; 
      font-size: 1rem;
      font-weight: bold;
      padding-bottom: 2rem; 
    }
} 

.radioForm {
  font-family: 'Indie Flower', cursive;
  padding-bottom: 3rem; 
  font-size: 1.5rem;
  display: flex;
  flex-direction: row; 
  align-items: center; 
  font-weight: bold;
}

.radioButtons {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 2rem;
  }

@media only screen and (max-width: 500px) {
  .radioButtons {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: bold;
    margin-bottom: 2rem;
    }
}

input[type="radio"]:hover {
  box-shadow:0px 0px 10px rgb(55, 77, 119);
}

input[type="radio"]:hover + label {
 color: rgb(185, 180, 180);
}

input[type="radio"]:checked + label {
 color: rgb(185, 180, 180);
}

@media only screen and (max-width: 500px) {
  label {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  } 
} 

@media only screen and (min-width: 501px){
label {
  display: flex;
  flex-direction: row;
  margin-left: 2rem;
}
}

.download {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

.downloadButton {
  margin-bottom: 3rem;
  height: 6rem;
}

#promptDownload {
  display: none;
}

.writing {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#UserWriting {
  height: 30rem;
  width: 15rem;
  margin-top: 1rem;
  padding-top: 1rem;
  word-wrap: break-word;
  word-break: break-word;
}

html {
  background-image: url("http://backgrounds.picaboo.com/download/4a/a4/4a4c7b6d71194dcb87c4d6c1073b2d07/collegeruled.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover; 
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Indie Flower', cursive;
  margin-top: 2rem;
  margin-bottom: 5rem;
  margin-left: 3rem;
  margin-right: 3rem;
  padding-right: 2rem;
  padding-left: 2rem;
}

@media screen and (max-width: 480px) {
  .App {
    margin-top: 1rem;
    margin-bottom: 2rem;
    margin-left: 1rem;
    margin-right: 1rem;

  }
}

h1 {
  text-align: center;
  font-family: 'Indie Flower', cursive;
  margin-top: 5rem;
  margin-bottom: 2rem;
  font-size: 3.5rem;
}

@media screen and (max-width: 480px) { 
  h1 {
    text-align: center;
    font-family: 'Indie Flower', cursive;
    margin-top: 2rem;
    margin-bottom: 1rem;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

